import styles from './Screenshot.module.css'
import heart from '../../images/heart.png'
import bottle from '../../images/bottle.png'
import DownloadContainer from '../downloadContainer/DownloadContainer'
import Chat from '../chat/Chat'

export default function Screenshot() {
	return (
		<>
			<div className={styles.container}>
				<div className={styles.counter}>
					<div className={styles.hearts}>
						<img src={heart} className={styles.heart} />
						<div className={styles.count}>99</div>
					</div>
					<div className={styles.table}>
						<img src={bottle} className={styles.bottle} alt="logo" />
						<p className={styles.tableNumber}>#99999</p>
					</div>
				</div>
				<Chat />
				<DownloadContainer />
			</div>
		</>
	)
}
