import styles from './Chat.module.css'

export default function Chat() {
	return (
		<>
			<div className={styles.container}>
				<div className={styles.message}>
					<div className={styles.ava} style={{ backgroundColor: 'rgba(244, 93, 162, 0.2)' }}></div>
					<div className={styles.body} style={{ backgroundColor: 'rgba(244, 93, 162, 0.1)' }}>
						<span className={styles.nickname} style={{ backgroundColor: 'rgba(244, 93, 162, 0.2)', width: '4rem' }}></span>
						<span className={styles.text} style={{ width: '10rem' }}></span>
						<span className={styles.text} style={{ width: '4rem' }}></span>
					</div>
				</div>
				<div className={styles.message}>
					<div className={styles.ava} style={{ backgroundColor: 'rgba(244, 93, 162, 0.2)' }}></div>
					<div className={styles.body} style={{ backgroundColor: 'rgba(244, 93, 162, 0.1)' }}>
						<span className={styles.nickname} style={{ backgroundColor: 'rgba(244, 93, 162, 0.2)', width: '5rem' }}></span>
						<span className={styles.text} style={{ width: '8rem' }}></span>
						<span className={styles.text} style={{ width: '2rem' }}></span>
						<span className={styles.text} style={{ width: '4rem' }}></span>
					</div>
				</div>
				<div className={styles.message}>
					<div className={styles.ava} style={{ backgroundColor: 'rgba(148, 127, 238, 0.2)' }}></div>
					<div className={styles.body} style={{ backgroundColor: 'rgba(148, 127, 238, 0.1)' }}>
						<span className={styles.nickname} style={{ backgroundColor: 'rgba(148, 127, 238, 0.2)', width: '3.6rem' }}></span>
						<span className={styles.text} style={{ width: '6rem' }}></span>
						<span className={styles.text} style={{ width: '6rem' }}></span>
					</div>
				</div>
			</div>
		</>
	)
}
